import fetchobj from '@/utils/fetch'
let fetch = fetchobj.fetch;

export const Article = {
   
  create(data) {
    return fetch('post', '/articles', data)
  },

  get(data) {
    return fetch('get', '/articles', data)
  },

  getUserList(data){
    return fetch('post', '/articles/userList', data)
  }

}
