<template>
  <div class="app-container">
    <div class="filter-container">
      <!-- <el-select
        v-model="listQuery.userId"
        multiple
        placeholder="请选择姓名"
        style="vertical-align: middle;
    margin-bottom: 10px;"
      >
        <el-option
          v-for="item in userList"
          :key="item.userId"
          :label="item.name"
          :value="item.userId"
        ></el-option>
      </el-select>-->
      <div class="filter-item" style="margin-right:20px">
        <el-date-picker clearable v-model="listQuery.createTime" type="date"></el-date-picker>
      </div>
      <el-button class="filter-item" type="primary" icon="el-icon-search" @click="search">查询</el-button>
    </div>
    <el-tabs
      :tab-position="tabPosition"
      v-model="currentName"
      style="margin-top:10px;"
      type="border-card"
    >
      <el-tab-pane
        v-for="(item,index) in userList"
        :key="index"
        :label="item.name"
        :name="item.userId"
      >
        <div v-if="currentName==item.userId" class="article-table">
          <el-table
            v-loading="listLoading"
            :data="dataList"
            fit
            :show-header="false"
            style="width: 100%"
          >
            <el-table-column width="130px" align="center" label="创建时间">
              <template slot-scope="scope">
                <el-tag type="warning">
                  <i class="el-icon-time"></i>
                  <span>{{ scope.row.createTime | parseTime('{y}-{m}-{d}') }}</span>
                </el-tag>
              </template>
            </el-table-column>

            <el-table-column width="30px" align="center">
              <template slot-scope="scope">
                <div class="article-timeline"></div>
              </template>
            </el-table-column>

            <el-table-column width="120px" align="left" label="姓名">
              <template slot-scope="scope">
                <el-tag>{{ scope.row.author }}</el-tag>
              </template>
            </el-table-column>

            <el-table-column width="500px" align="center" label="内容">
              <template slot-scope="scope">
                <el-card class="box-card">
                  <div class="article-content" v-html="scope.row.content"></div>
                </el-card>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </el-tab-pane>
    </el-tabs>

    <pagination
      style="margin-top:0px;"
      v-show="total>10"
      :total="total"
      :page.sync="listQuery.page"
      :limit.sync="listQuery.limit"
      @pagination="getList"
    />
  </div>
</template>

<script>
import { Article } from "@/api/article";
import Pagination from "@/components/Pagination"; // Secondary package based on el-pagination
import { mapGetters } from "vuex";
export default {
  name: "ArticleList",
  components: { Pagination },
  computed: {
    ...mapGetters(["name", "userId", "departmentId", "roles"])
  },
  data() {
    return {
      currentName: null,
      tabPosition: "top",
      dataList: [],
      total: 0,
      listLoading: true,
      listQuery: {
        userId: [],
        createTime: "",
        page: 1,
        limit: 10
      },
      // 所能看到的用户集合
      userList: []
    };
  },
  created() {
    this.getUserList();
    this.listQuery.userId = [...this.userList];
    // this.getList();
  },
  watch: {
    currentName: {
      deep: true,
      handler(val) {
        if (val !== "-1") {
          this.listQuery.userId = [val];
        } else {
          this.listQuery.userId = [];
          for (let item of this.userList) {
            if (item.userId != "-1") {
              this.listQuery.userId.push(item.userId);
            }
          }
        }
        this.getList();
      }
    }
  },
  methods: {
    search() {
      this.listQuery.page = 1;
      this.getList();
    },
    getList() {
      this.listLoading = true;
      let params = JSON.parse(JSON.stringify(this.listQuery));
      params.userId = JSON.stringify(params.userId);
      Article.get(params).then(response => {
        this.dataList = response.data;
        this.total = response.total;
        this.listLoading = false;
      });
    },
    /**
     * @description 获取用户名下的所有子用户
     * @function {function name}
     * @return {type} {description}
     */
    getUserList() {
      Article.getUserList({
        userId: this.userId,
        roles: this.roles
      }).then(response => {
        if (response.data.length == 1) {
          this.userList = [...response.data];
          this.currentName=this.userList[0].userId;
        } else {
          this.userList = [
            {
              name: "全部",
              userId: "-1"
            },
            ...response.data
          ];
          this.currentName="-1";
        }
      });
    },
    handleSizeChange(val) {
      this.listQuery.limit = val;
      this.getList();
    },
    handleCurrentChange(val) {
      this.listQuery.page = val;
      this.getList();
    }
  }
};
</script>


